import { Component, OnInit } from '@angular/core';
import { details } from '../account';

@Component({
  selector: 'app-account-list',
  templateUrl: './account-list.component.html',
  styleUrls: ['./account-list.component.css']
})
export class AccountListComponent implements OnInit {

  constructor() { }

  public accountDetails = details;
  public array1 = [];
  public array2 = [];
  public accLoaded=false;
  public nameCount:number = 0;
  public noCount:number = 0;
  public showMe=true;

  ngOnInit() {
    this.basicLoad(); 
  }

  public basicLoad(): void {
    if (this.accountDetails.length > 3) {
      this.array1 = this.accountDetails.slice(0,3);
      this.array2 = this.accountDetails.slice(3);
    }
}

  public loadMe(): void {
      this.accLoaded=true;
      this.showMe=false;
  }
  
  public sortByName(): void {
    this.nameCount +=1;
    if(this.nameCount%2==0){
      this.accountDetails.sort((a,b)=> b.account.localeCompare(a.account));
    } else {
        this.accountDetails.sort((a,b)=> a.account.localeCompare(b.account));
    }
    this.basicLoad();
}
public sortByNo(): void {
  
  this.noCount +=1;
  if(this.noCount%2==0){
    this.accountDetails.sort((a,b)=> b.cash - a.cash);
  } else {
      this.accountDetails.sort((a,b)=> a.cash - b.cash);
  }
  this.basicLoad();
}

}
